.search_widget {

    .search_widget_results {
        &__content {
            position: absolute;
            background-color: #f9f9f9;
            min-width: 160px;
            box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
            padding: 12px 16px;
            z-index: 1;
        }

        &--show {
            display: inline-block;
        }

        &--hide {
            display: none;
        }
    }

    &__result {
        background-color: rgba(255, 255, 255, .9);
        padding: 5px;

        &:hover {
            cursor: pointer;
            background-color: rgba(0, 255, 0, .2);
        }

        img {
            width: 100%;
        }
    }
}
